import selectItem from '../../components/my-components/editBlock-SelectItem.vue'
import inputItem from '../../components/my-components/editBlock-InputItem.vue'
import pageMixin from '../../components/my-components/PageMixins.vue'
let DataFrom = {};
export default {
	mixins:{
		pageMixin
	},
	components: {
		selectItem,
		inputItem
	},
	data() {
		return {
			userName: '',
			userTel: '',
			userRoleName: '',
			realName: '',
			wxInfo: {}
		}
	},
	methods: {
		initPage() {
			this.Request('myInfo').then(res => {
				this.userName = res.user.username;
				this.userTel = res.user.contact_mobile;
				this.userRoleName = res.user.role_name;
				this.realName = res.user.contact;
			})
		},
		toPage(val) {
			if (val == 'name') {
				this.Cacher.setData('myself', {
					userName: this.userName,
					userTel: this.userTel,
					userRoleName: this.userRoleName,
					realName: this.realName,
					needChange: {
						name: '姓名',
						id: 'realName'
					}
				})
				uni.navigateTo({
					url: '../../pagesSelf/pages/myName?from=myself'
				})
			} else if (val == 'tel') {
				this.Cacher.setData('myself', {
					userName: this.userName,
					userTel: this.userTel,
					userRoleName: this.userRoleName,
					realName: this.realName,
					needChange: {
						name: '联系方式',
						id: 'userTel'
					}
				})
				uni.navigateTo({
					url: '../../pagesSelf/pages/myName?from=myself'
				})
			} else if (val == 'password') {
				uni.navigateTo({
					url: '../../pagesSelf/pages/password?from=myself'
				})
			}
		},
		clickManager() {},
		bindWX() {
			this.closePageLoading();
			this.Request('bindWechat', {
				encrypted_data: '',
				session_key: '',
				iv: '',
				user_id: ''
			})
			this.Toast('绑定微信成功')
		},
		leave() {
			this.closePageLoading();
			this.Dialog.confirm({
				title: '',
				message: '您确认退出当前账号吗？'
			}).then(() => {
				this.Cacher.setData('self', {
					from: 'self'
				})
				uni.reLaunch({
					url: '../login/index?from=self'
				})
			}).catch(() => {
				// on cancel
			});
		}
	},
	onShow() {
		let info = this.Cacher.getData('login') || {};
		this.wxInfo = info.userInfo;
		this.initPage();
	}
}
